export default Vue.extend({
  name: "App",
  components: {
    LeftPanel,
    RightPanel,
    RequestAlert,
  },
  data() {
    return {
      dismissSecs: 10,
      dismissCountDown: 0,
    };
  },
  computed: {
    ...mapState("RequestStore", ["requestErrorMessage"]),
    ...mapGetters("RequestStore", ["requestError"]),
  },
  methods: {
