


















import Vue from "vue";
import { mapActions, mapMutations, mapState } from "vuex";
import Item from "./Item.vue";

export default Vue.extend({
  components: { Item },
  name: "ListItems",
  computed: {
    ...mapState("WalletsStore", ["wallets"]),
  },
  methods: {
    ...mapActions("WalletsStore", ["getWallets"]),
    ...mapMutations("WalletsStore", ["sortWallets"]),
  },
  mounted() {
    this.getWallets();
  },
});
